import React from 'react';
import styled, { keyframes } from 'styled-components';
import Hangman from '../Reusable/Hangman/Hangman';
import StartGameButton from '../Reusable/Buttons/StartGameButton';

const EndGameWrapper = styled.div`
position: absolute;
top: 0;
bottom: 36% ;
left: 0;
right: 0;
display: flex;
flex-flow: column nowrap;
justify-content: space-around;
align-items: center;
background-color:${({ theme }) => theme.colors.deepRed};
transform: ${({ isGameEnded }) => isGameEnded ? 'translateY(0)' : 'translateY(-100%)'};
transition: .4s .4s linear all;
color: ${({ theme }) => theme.colors.gray};
`


const IsWonWrapper = styled.h2`
letter-spacing: 2px;
font-size: 1.4em;
`

const EndGamePage = ({ isGameEnded, isGameWon, phraseToGuess, startGame }) => {

    const isGameWonContent = isGameWon ? 'Good job!' : 'Try again'
    const phrase = !isGameWon && phraseToGuess.map(phrase => phrase.letter);

    return (
        <EndGameWrapper
            isGameWon={isGameWon}
            isGameEnded={isGameEnded}>
            <IsWonWrapper>
                {isGameWonContent}
            </IsWonWrapper>
            <h3>{phrase}</h3>
            <StartGameButton
                startGame={startGame}
                phraseToGuess={phraseToGuess}>Play again
            </StartGameButton>
        </EndGameWrapper>
    );
}
export default EndGamePage;